.home {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-rows: repeat(2, auto);
  height: 100vh;
  box-shadow: 0 0 20px 5px #3e3028 inset;
  background-color: #d7ccc8;
  background-image: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 0h20v20H0V0zm10 17a7 7 0 1 0 0-14 7 7 0 0 0 0 14zm20 0a7 7 0 1 0 0-14 7 7 0 0 0 0 14zM10 37a7 7 0 1 0 0-14 7 7 0 0 0 0 14zm10-17h20v20H20V20zm10 17a7 7 0 1 0 0-14 7 7 0 0 0 0 14z' fill='%23a1887f' fill-opacity='0.21' fill-rule='evenodd'/%3E%3C/svg%3E");
}
.rules {
  font-size: large;
  text-align: center;
  align-self: start;
  padding: 0 20px;
}
.banner {
  width: 100vw;
  max-width: 640px;
}
button {
  width: 100px;
  height: 35px;
  background-color: #5d4037;
  border: none;
  color: #efebe9;
  border-radius: 20px;
}
